:root {
  --primary-color: #2c3e50;
  --secondary-color: #3498db;
  --accent-color: #e74c3c;
  --background-color: #ecf0f1;
  --text-color: #333;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  margin-left: 20px;
}

nav ul li a {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: var(--secondary-color);
}

.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: calc(100vh - 80px);
}

.hero-content {
  max-width: 50%;
}

.hero h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
  color: var(--primary-color);
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.cta-button {
  display: inline-block;
  background-color: var(--secondary-color);
  color: white;
  padding: 12px 24px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: var(--primary-color);
}

.hero-image {
  width: 40%;
  height: 400px;
  background-color: #ddd; /* Placeholder for image */
  border-radius: 10px;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin: 50px 0;
}

.project-item {
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.project-item:hover {
  transform: translateY(-5px);
}

.project-image {
  height: 200px;
  background-color: #ddd; /* Placeholder for image */
}

.project-info {
  padding: 20px;
}

.project-info h3 {
  margin-bottom: 10px;
  color: var(--primary-color);
}

.skills {
  background-color: white;
  border-radius: 10px;
  padding: 40px;
  margin: 50px 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.skills h2 {
  color: var(--primary-color);
  margin-bottom: 30px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.skill-item {
  text-align: center;
}

.skill-icon {
  font-size: 3rem;
  color: var(--secondary-color);
  margin-bottom: 10px;
}

.skill-detail {
  padding: 50px 0;
}

.back-button {
  display: inline-block;
  margin-bottom: 20px;
  padding: 10px 20px;
  background-color: var(--secondary-color);
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.skill-detail h2 {
  margin-bottom: 20px;
}

.skill-detail ul {
  list-style-type: none;
  padding: 0;
}

.skill-detail ul li {
  margin-bottom: 10px;
}

/* Contact Section */
.contact {
  background-color: var(--primary-color);
  color: white;
  padding: 50px;
  border-radius: 10px;
  margin: 50px 0;
}

.contact h2 {
  margin-bottom: 30px;
}

form {
  display: grid;
  gap: 20px;
}

input, textarea {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
}

button {
  background-color: var(--accent-color);
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: var(--secondary-color);
}

/* Footer */
footer {
  text-align: center;
  padding: 20px 0;
  margin-top: 50px;
  border-top: 1px solid #ddd;
}

.special-project {
  grid-column: 1 / -1; /* This makes the special project take up the full width */
  background-color: var(--background-color);
  padding: 20px;
  border-radius: 10px;
  margin-top: 30px;
  text-align: center;
}

.special-project h3 {
  color: var(--primary-color);
  margin-bottom: 10px;
}

.special-project p {
  margin-bottom: 20px;
}

.special-project-button {
  display: inline-block;
  padding: 12px 24px;
  background-color: var(--accent-color);
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.special-project-button:hover {
  background-color: var(--secondary-color);
  transform: translateY(-2px);
}